function Modal(props){
  return (
    <>
      <div className="modal" style={{background : props.color}}>
        <h4>{props.subj[props.title]}</h4>
        <p>날짜</p>
        <p>상세내용</p>
        <button onClick={()=>{
          let copy = [...props.subj];
          copy[0] = "여자코트 추천";
           props.setSubj(copy);
          //props.setSubj(props.subj[0] = "여자코트추천")
        }}>글수정</button>
      </div>
    </>
  );
}
